import Vue from 'vue'
import Router from 'vue-router'
import SignChecker from '@/components/SignChecker'
import SellerExporter from '@/components/SellerExporter'
import LiveTemplateMgr from '@/components/LiveTemplateMgr'
import SuggestBox from '@/components/SuggestBox'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'SignChecker',
      component: SignChecker
    },
    {
      path: '/sellerExporter',
      name: 'SellerExporter',
      component: SellerExporter
    },
    {
      path: '/liveTemplateMgr',
      name: 'LiveTemplateMgr',
      component: LiveTemplateMgr
    },
    {
      path: '/suggestBox',
      name: 'SuggestBox',
      component: SuggestBox
    }
  ]
})



