<template>
    <div id="app">
        <div class="layout">
            <Menu mode="horizontal" theme="light" :active-name="menuActive" @on-select="onSelectMenu">
                <div class="layout-logo"></div>
                <div class="layout-nav">
                    <MenuItem name="signChecker">
                    <Icon type="android-checkmark-circle"></Icon>主播签名检查</MenuItem>
                    <MenuItem name="sellerExporter">
                    <Icon type="ios-download-outline"></Icon>品类主播导出</MenuItem>
                    <MenuItem name="liveTemplateMgr">
                    <Icon type="ios-undo-outline"></Icon>开播模板还原</MenuItem>
                    <MenuItem name="suggestBox">
                    <Icon type="paper-airplane"></Icon>意见箱</MenuItem>
                </div>
            </Menu>
            <div class="layout-content">
                <router-view :key="$route.path"></router-view>
            </div>
            <div class="layout-copy">
                电商项目组
            </div>
        </div>
    </div>
</template>
<script>
export default {
  name: 'App',
  computed: {
    menuActive () {
      let path = this.$route.path
      if (path.indexOf('sellerExporter') !== -1) {
        return 'sellerExporter'
      } else if (path.indexOf('signChecker') !== -1) {
        return 'signChecker'
      } else if (path.indexOf('liveTemplateMgr') !== -1) {
        return 'liveTemplateMgr'
      } else if (path.indexOf('suggestBox') !== -1) {
        return 'suggestBox'
      }
      return 'signChecker'
    }
  },
  methods: {
    onSelectMenu (name) {
      switch (name) {
        case 'signChecker':
          this.$router.push({name: 'SignChecker'})
          break
        case 'sellerExporter':
          this.$router.push({name: 'SellerExporter'})
          break
        case 'liveTemplateMgr':
          this.$router.push({name: 'LiveTemplateMgr'})
          break
        case 'suggestBox':
          this.$router.push({name: 'SuggestBox'})
          break
        default:
          console.log('unknown menu item.')
          break
      }
    }
  }
}
</script>
<style scoped>
#app {
  overflow: hidden;
}

.layout {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
}

.layout-content {
  min-height: 200px;
  padding: 10px;
  /* overflow: hidden; */
  background: #fff;
  border-radius: 4px;
}

.layout-copy {
  text-align: center;
  padding: 10px 0 20px;
  color: #9ea7b4;
}

/* 设置滚动条的样式 */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

/* 滚动槽 */
::-webkit-scrollbar-track {
  border-radius: 10px;
}

/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.2);
}
</style>



