<template>
  <div>
    <Form class="queryForm" ref="formInline" :model="queryParams" inline>
      <FormItem v-for="filter in filters" :key="filter.key" :prop="filter.name">
        <Input v-if="filter.type==='text'" type="text" v-model="queryParams[filter.key]" 
        :placeholder="filter.placeholder?filter.placeholder:filter.name">
        </Input>
        <Input v-if="filter.type==='textarea'" type="textarea" v-model="queryParams[filter.key]" 
        :placeholder="filter.placeholder?filter.placeholder:filter.name"
        :autosize="true">
        </Input>
        <Select v-else-if="filter.type==='select'" v-model="queryParams[filter.key]" 
        :placeholder="filter.placeholder?filter.placeholder:filter.name" 
        :style="{width: filter.width}">
          <Option v-for="item in filter.options.list" :key="item.key" :value="item.key">{{item.name}}</Option>
        </Select>
      </FormItem>
      <FormItem>
        <Button v-if="showSearch && mode === 'query'" type="primary" @click="clearAndSearch" :loading="loadingData">搜索</Button>
        <Button v-if="showNew" type="success" @click="openEditor">新增</Button>
        <Button v-for="btn in btns" :key="btn.name" :type="btn.type" @click="btn.on">{{btn.name}}</Button>
        <transition name="fade">
          <Button v-if="showBatchDelete" type="error" @click="batchDelete">批量删除</Button>
        </transition>
        <Button v-if="mode === 'export' || mode === 'both'" type="error" @click="exportData" :loading="exportingData">导出</Button>           
      </FormItem>
    </Form>
    <div v-if="mode === 'query' || mode === 'both'">
      <pageable-table :loading="loadingData" :col="col" :data="result" :totalSize="totalSize"
                      @on-change="onPageNoChange"
                      @on-page-size-change="onPageSizeChange"
                      @on-selection-change="onSelectionChange"></pageable-table>
    </div>
  </div>
</template>

<script>
  import ReqUtils from '@/common/ReqUtils'
  import RespUtils from '@/common/RespUtils'
  import FileDownload from '@/common/FileDownload'
  import PageableTable from './PageableTable'
  import Qs from 'qs'

  export default {
    name: 'common-mgr',
    components: {
      PageableTable
    },
    props: {
      showSearch: {
        'type': Boolean,
        'default': true
      },
      showNew: {
        'type': Boolean,
        'default': true
      },
      params: {
        'type': Object,
        'default': () => {
          return {}
        }
      },
      searchUrl: String,
      exportUrl: String,
      deleteUrl: String,
      startUrl: String,
      stopUrl: String,
      filters: {
        'type': Array,
        'default': () => {
          return [{
            key: '',
            name: '',
            type: '',
            options: {}
          }]
        }
      },
      resultCol: {
        'type': Array,
        'default': () => {
          return []
        }
      },
      btns: {
        'type': Array,
        'default': () => {
          return []
        }
      },
      mode: {
        'type': String,
        'default': 'query'
      },
      defaultSearch: {
        'type': Boolean,
        'default': true
      }
    },
    data () {
      return {
        result: [],
        queryParams: {},
        totalSize: 0,
        selectedRow: -1,
        pageNo: 1,
        pageSize: 50,
        editMode: false,
        loadingData: false,
        exportingData: false,
        id: -1,
        editFields: {},
        showBatchDelete: false,
        selectedRows: []
      }
    },
    computed: {
      col () {
        let col = []
        for (let i = 0; i < this.resultCol.length; i++) {
          const item = this.resultCol[i]
          if (item.display === false) {
            continue
          }
          if (item.render || !item.type) {
            col.push(item)
            continue
          }
          switch (item.type) {
            case 'date':
              this.parseDateCell(item)
              col.push(item)
              break
            case 'button':
              this.parseBtnCell(item)
              if (!item.invalid) {
                col.push(item)
              }
              break
            case 'code':
              this.parseCodeCell(item)
              col.push(item)
              break
            case 'switch':
              this.parseSwitchCell(item)
              col.push(item)
              break
            default:
              col.push(item)
              break
          }
        }
        return col
      }
    },
    methods: {
      exportData () {
        if (!this.exportUrl) {
          this.$Notice.error({
            title: '导出失败',
            desc: '没有获取到导出URL，请联系开发人员修复。',
            duration: 5
          })
          return
        }
        this.exportingData = true
        let params = Qs.stringify(ReqUtils.combinParams(this.params, this.queryParams, this))
        this.$http.get(this.exportUrl + '?' + params, {
          responseType: 'blob'
        })
          .then(resp => {
            this.exportingData = false
            console.log(resp)
            const disposition = resp.headers['content-disposition']
            const ss = disposition.split(';')
            const dispositionMap = {}
            ss.forEach(value => {
              const pairArray = value.split('=')
              if (pairArray.length === 1) {
                dispositionMap[pairArray[0]] = undefined
              }
              if (pairArray.length > 1) {
                dispositionMap[pairArray[0]] = pairArray[1]
              }
            })
            FileDownload.download(resp.data, decodeURI(dispositionMap.filename))
            this.$emit('on-export', this.result)
            this.showBatchDelete = false
            this.selectedRows = []
          })
      },
      parseDateCell (item) {
        item.render = (h, params) => {
          let moment = Number(params.row[item.key])
          if (isNaN(moment)) {
            return h('div')
          }
          const formattedDate = this.$moment(moment).format('YYYY-MM-DD HH:mm:ss')
          return h('div', formattedDate)
        }
      },
      parseBtnCell (item) {
        let data = item.options.data
        item.render = (h, params) => {
          let content = []
          data.forEach(x => {
            if (x.condition && Object.prototype.toString.call(x.condition) === '[object Function]' && !x.condition(params)) {
              return
            }
            let hc = h('Button', {
              props: {
                type: (() => {
                  if (x.func !== 'stop') {
                    return x.type
                  }
                  const normalTag = x.status || 1
                  const revertTag = x.rstatus || 2
                  if (params.row.status === normalTag) {
                    return 'error'
                  } else if (params.row.status === revertTag) {
                    return 'success'
                  }
                })(),
                size: 'small'
              },
              style: {
                marginRight: '5px'
              },
              on: {
                click: () => {
                  if (Object.prototype.toString.call(x.click) === '[object Function]') {
                    x.click(params.index, params.row)
                  } else {
                    switch (x.func) {
                      case 'update':
                        this.openEditor(undefined, params.index)
                        break
                      case 'delete':
                        this.remove(params.index, params.row, x.tip)
                        break
                      case 'stop':
                        const normalTag = x.status || 1
                        const revertTag = x.rstatus || 2
                        if (params.row.status === normalTag) {
                          this.stop(params.index, params.row, x.tip)
                        } else if (params.row.status === revertTag) {
                          this.start(params.index, params.row, x.rtip)
                        } else {
                          this.$Notice.info({
                            title: '暂无此操作'
                          })
                        }
                    }
                  }
                }
              }
            }, (() => {
              if (x.func !== 'stop') {
                return x.name
              }
              const normalTag = x.status || 1
              const revertTag = x.rstatus || 2
              if (params.row.status === normalTag) {
                return x.name
              } else if (params.row.status === revertTag) {
                return x.rname
              }
            })())
            content.push(hc)
          })
          return h('div', content)
        }
      },
      parseCodeCell (item) {
        const codeTable = item.options.codeTable
        item.render = (h, params) => {
          const type = params.row[item.key]
          if (item.options.color) {
            if (codeTable[type]) {
              return h('Tag', {
                props: {
                  color: item.options.color[type] || 'info'
                },
                domProps: {
                  innerHTML: codeTable[type]
                }
              })
            } else {
              let def = item.options.defValue
              if (!def) {
                def = '异常'
              }
              return h('Tag', def)
            }
          } else {
            if (codeTable[type]) {
              return h('div', codeTable[type])
            } else {
              let def = item.options.defValue
              if (!def) {
                def = '异常'
              }
              return h('div', def)
            }
          }
        }
      },
      parseSwitchCell (item) {
        const key = item.key
        let _this = this
        item.render = (h, params) => {
          const value = item.options.codeTable[params.row[key]] || item.options.defValue || false
          return h('i-switch', {
            props: {
              value: value,
              size: 'large'
            },
            on: {
              'on-change': status => {
                _this.$Modal.confirm({
                  title: '确认操作',
                  content: '该操作将会影响线上数据展示，您确认吗？',
                  onOk: () => {
                    _this.$http.post(item.options.url, {id: params.row.id, key: item.key, status: status})
                      .then(resp => {
                        if (!RespUtils.checkResponse(resp, _this)) {
                          _this.search()
                          return
                        }
                        _this.$Notice.success({
                          title: '操作成功',
                          duration: 5
                        })
                        _this.search()
                      })
                  },
                  onCancel: () => {
                    _this.search()
                  }
                })
              }
            }
          }, [
            h('span', {
              slot: 'open'
            }, item.options.open),
            h('span', {
              slot: 'close'
            }, item.options.close)
          ])
        }
      },
      onPageNoChange (pageNo) {
        this.pageNo = pageNo
        this.search()
      },
      onPageSizeChange (pageSize) {
        this.pageSize = pageSize
        this.search()
      },
      onSelectionChange (selection) {
        console.log(selection)
        if (selection && selection.length > 0) {
          this.showBatchDelete = true
        } else {
          this.showBatchDelete = false
        }
        this.selectedRows = selection
      },
      openEditor (e, index) {
        if (index || index === 0) {
          this.selectedRow = index
          this.id = this.result[this.selectedRow].id
          this.$emit('on-open-editor', this.selectedRow, this.result[this.selectedRow])
        } else {
          this.selectedRow = -1
          this.id = -1
          this.$emit('on-open-editor', this.selectedRow)
        }
        this.editMode = true
      },
      clearAndSearch () {
        this.pageNo = 1
        this.search()
      },
      search () {
        if (this.mode !== 'both' && this.mode !== 'query') {
          return
        }
        if (!this.searchUrl) {
          this.$Notice.error({
            title: '查询失败',
            desc: '没有获取到搜索URL，请联系开发人员修复。',
            duration: 5
          })
          return
        }
        this.loadingData = true
        let params = Qs.stringify(ReqUtils.combinParams(this.params, this.queryParams, this))
        this.$http.get(this.searchUrl + '?' + params)
          .then(resp => {
            this.loadingData = false
            if (!RespUtils.checkResponse(resp, this)) {
              return
            }
            const data = resp.data.data
            this.result = data.data
            this.totalSize = data.total
            this.$emit('on-search', this.result)
            this.showBatchDelete = false
            this.selectedRows = []
          })
      },
      reset () {
        this.queryParams = {}
      },
      remove (index, record, tip) {
        this.$Modal.confirm({
          title: tip && tip.title ? tip.title : '确认删除',
          content: tip && tip.content ? tip.content : '删除数据将无法恢复，您确认删除吗？',
          onOk: () => {
            this.doRemove(index, record)
          }
        })
      },
      doRemove (index, record) {
        let _this = this
        _this.$http.delete(ReqUtils.matchUrl(this.deleteUrl, {id: record.id}))
          .then(resp => {
            if (!RespUtils.checkResponse(resp, this)) {
              return
            }
            _this.$Message.success({
              content: '删除成功',
              duration: 5
            })
            _this.search()
          })
      },
      stop (index, record, tip) {
        this.$Modal.confirm({
          title: tip && tip.title ? tip.title : '确认停用',
          content: tip && tip.content ? tip.content : '停用后可能会对线上数据产生影响，您确认停用吗？',
          onOk: () => {
            this.doStop(index, record)
          }
        })
      },
      doStop (index, record) {
        let _this = this
        _this.$http.post(ReqUtils.matchUrl(this.stopUrl, {id: record.id}))
          .then(resp => {
            if (!RespUtils.checkResponse(resp, this)) {
              return
            }
            _this.$Message.success({
              content: '停用成功',
              duration: 5
            })
            _this.search()
          })
      },
      start (index, record, tip) {
        this.$Modal.confirm({
          title: tip && tip.title ? tip.title : '确认启用',
          content: tip && tip.content ? tip.content : '启用后可能会对线上数据产生影响，您确认启用吗？',
          onOk: () => {
            this.doStart(index, record)
          }
        })
      },
      doStart (index, record) {
        let _this = this
        _this.$http.post(ReqUtils.matchUrl(this.startUrl, {id: record.id}))
          .then(resp => {
            if (!RespUtils.checkResponse(resp, this)) {
              return
            }
            _this.$Message.success({
              content: '启用成功',
              duration: 5
            })
            _this.search()
          })
      },
      batchDelete () {
        if (!this.selectedRows || this.selectedRows.length <= 0) {
          this.$Notice.warn({
            title: '没有指定需要删除的数据',
            duration: 5
          })
          return
        }
        this.$Modal.confirm({
          title: '确认删除',
          content: '数据删除无法恢复，您确认删除吗?',
          onOk: () => {
            this.$emit('on-batch-delete', this.selectedRows)
          }
        })
      }
    },
    mounted () {
      if (this.defaultSearch) {
        this.search()
      }
    },
    watch: {
      params () {
        if (this.defaultSearch) {
          this.reset()
          this.search()
        }
      }
    }
  }
</script>

<style scoped>
  .queryForm {
    margin: 10px 0;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */
  {
    opacity: 0
  }
</style>



