<template>
  <div>
    <div class="tableContainer" v-if="data && data.length > 0">
      <Table :columns="col" :data="data" :loading="loading"
             @on-selection-change="onSelectionChange"></Table>
      <Page class="page"
            v-if="pageable"
            :page-size="pageSize"
            :page-size-opts="pageSizeOpts"
            :current="pageNo"
            :total="totalSize"
            @on-change="onPageChange"
            @on-page-size-change="onPageSizeChange"
            show-sizer></Page>
    </div>
    <div v-else-if="showNoData">
      <p style="margin: 20px auto; text-align: center">当前还没有数据，快去添加吧！</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'pageable-table',
    props: {
      col: {
        'type': Array,
        'default': () => {
          return []
        }
      },
      data: {
        'type': Array,
        'default': () => {
          return []
        }
      },
      totalSize: {
        'type': Number,
        'default': 0
      },
      pageSize: {
        'type': Number,
        'default': 50
      },
      pageNo: {
        'type': Number,
        'default': 1
      },
      pageSizeOpts: {
        'type': Array,
        'default': () => {
          return [20, 50, 100]
        }
      },
      pageable: {
        'type': Boolean,
        'default': true
      },
      showNoData: {
        'type': Boolean,
        'default': true
      },
      loading: {
        'type': Boolean,
        'default': false
      }
    },
    methods: {
      onPageChange (pageNo) {
        this.$emit('on-change', pageNo)
      },
      onPageSizeChange (pageSize) {
        this.$emit('on-page-size-change', pageSize)
      },
      onSelectionChange (selection) {
        this.$emit('on-selection-change', selection)
      }
    }
  }
</script>

<style scoped>
  .page {
    margin: 20px 0;
    float: right;
  }

  .tableContainer {
    overflow: hidden;
  }
</style>



