<template>
  <Row>
    <Card>
      <p>说明：</p>
      <p>1. 此工具适用于在没有处罚中心的前提下，快速筛选出疑似违规设定个性签名的主播。</p>
      <p>2. 此工具采用关键词的方式筛选所有入驻商家的个性签名，默认使用的关键词为：<span style="color: red;">"微信", "wx", "vx", "v"</span>。支持自定义关键词，可在搜索栏的关键词输入框中输入多个关键词，回车分隔。</p>
      <p>3. 支持批量查找uid，回车分隔。</p>
      <p>4. 存在误报漏报，请小心甄别。</p>
    </Card>
    <common-mgr
      ref="groupMgr"
      searchUrl="/tools/querySign"
      :filters="filters"
      :resultCol="resultCol"
      :showNew="false"></common-mgr>
  </Row>
</template>
<script>
import CommonMgr from '@/components/CommonMgr'

export default {
  name: 'sign-checker',
  components: {CommonMgr},
  data () {
    return {
      filters: [
        {
          name: '主播uid',
          key: 'uids',
          type: 'textarea',
          placeholder: '批量主播uid, 回车分隔'
        },
        {
          name: '经营品类',
          key: 'category',
          type: 'select',
          width: '120px',
          options: {
            list: [
              {
                key: 'all',
                name: '全部'
              },
              {
                key: '翡翠玉石',
                name: '翡翠玉石'
              },
              {
                key: '翡翠玉石（广）',
                name: '翡翠玉石（广）'
              },
              {
                key: '彩色宝石',
                name: '彩色宝石'
              },
              {
                key: '文玩珠串',
                name: '文玩珠串'
              },
              {
                key: '手工艺',
                name: '手工艺'
              },
              {
                key: '其他',
                name: '其他'
              }
            ]
          }
        },
        {
          name: '自定义关键词',
          key: 'keys',
          type: 'textarea',
          placeholder: '自定义关键词, 回车分隔'
        },
        {
          name: '自动筛选?',
          key: 'filter',
          type: 'select',
          width: '100px',
          options: {
            list: [
              {
                key: '0',
                name: '全部'
              },
              {
                key: '1',
                name: '疑似违规'
              }
            ]
          }
        }
      ],
      resultCol: [
        {
          title: '商家uid',
          key: 'uid'
        },
        {
          title: '直播间号',
          key: 'sid'
        },
        {
          title: '经营品类',
          key: 'category'
        },
        {
          title: '昵称',
          key: 'nickname'
        },
        {
          title: '真实姓名',
          key: 'realname'
        },
        {
          title: '手机号',
          key: 'phone'
        },
        {
          title: '个性签名',
          key: 'sign',
          render: (h, params) => {
            let sections = []
            const signs = params.row.sign
            signs.forEach(value => {
              sections.push(h('span', {
                style: {
                  fontSize: value.level === 'info' ? '12px' : '20px',
                  color: value.level === 'info' ? '#495060' : 'red'
                }
              }, value.content))
            })
            return h('div', sections)
          }
        },
        {
          title: '违规标识',
          key: 'warn',
          render: (h, params) => {
            return h('Tag', {
              props: {
                color: params.row.warn ? 'yellow' : 'default'
              }
            }, params.row.warn ? '警告' : '正常')
          }
        }
      ]
    }
  }
}
</script>




