const ReqUtils = {
  combinParams (params1, params2, context) {
    const param = {}
    for (let p in params1) {
      if (params1.hasOwnProperty(p)) {
        param[p] = params1[p]
      }
    }
    for (let p in params2) {
      if (params2.hasOwnProperty(p)) {
        param[p] = params2[p]
      }
    }
    if (context && context.pageNo) {
      param.pageNo = context.pageNo
    }
    if (context && context.pageSize) {
      param.pageSize = context.pageSize
    }
    if (context && context.id) {
      param.id = context.id
    }
    return param
  },
  matchUrl (url, params) {
    return url.replace(/{([^\s]*?)}/g, word => {
      const originWord = word.substring(1, word.length - 1)
      return params[originWord]
    })
  }
}

export default ReqUtils



