<template>
  <Row>
    <Card>
       <p slot="title">
          <Icon type="information-circled"></Icon>
          说明
        </p>
      <p>这个小工具集合旨在帮助运营小伙伴从重复性的工作中解脱出来，所以，如果工作中有哪些不爽的地方需要解决的，请提到这里，反正我们也不一定会做～</p>
    </Card>
    <Card style="margin-top: 40px;">
      <Input type="textarea" v-model="msg" 
          placeholder="请写下你的期望"
          :autosize="{minRows: 2,maxRows: 5}"></Input>
      <Button style="margin-top: 20px;" type="success" :long="true" :loading="committing" @click="commitMsg">提交</Button>
    </Card>
    <common-mgr style="margin-top: 40px;"
      ref="suggestionBox"
      searchUrl="/tools/suggestions"
      :resultCol="resultCol"
      :showNew="false"></common-mgr>
  </Row>
</template>
<script>
import CommonMgr from '@/components/CommonMgr'
import RespUtils from '@/common/RespUtils'

export default {
  name: 'suggest-box',
  components: {CommonMgr},
  data () {
    return {
      committing: false,
      msg: '',
      resultCol: [
        {
          title: '提议人',
          key: 'username'
        },
        {
          title: '建议内容',
          key: 'content'
        },
        {
          title: '提交时间',
          key: 'createTime',
          type: 'date'
        }
      ]
    }
  },
  methods: {
    commitMsg () {
      if (!this.msg) {
        this.$Notice.error({
          title: '提交失败',
          desc: '请正确填写建议内容',
          duration: 5
        })
      }
      this.committing = true
      this.$http.post('/tools/suggest', {suggestion: this.msg})
        .then(resp => {
          if (!RespUtils.checkResponse(resp, this)) {
            return
          }
          this.msg = ''
          this.$refs.suggestionBox.search()
          this.committing = false
          this.$Notice.success({
            title: '提交成功',
            desc: '我们可能并不会尽快处理的',
            duration: 5
          })
        })
    }
  }
}
</script>



