<template>
  <common-mgr
    ref="liveTemplateMgr"
    searchUrl="/tools/liveTemplate"
    :filters="filters"
    :resultCol="resultCol"
    :showNew="false"
    :defaultSearch="false"></common-mgr>
</template>
<script>
import CommonMgr from '@/components/CommonMgr'
import RespUtils from '@/common/RespUtils'

export default {
  name: 'live-template-mgr',
  components: { CommonMgr },
  data () {
    return {
      filters: [
        {
          name: '频道号',
          key: 'sid',
          type: 'text',
          placeholder: '频道号, 必填!'
        }
      ],
      resultCol: [
        {
          title: '频道号',
          key: 'sid'
        },
        {
          title: '频道短号',
          key: 'asid'
        },
        {
          title: '频道名',
          key: 'name'
        },
        {
          title: '模板id',
          key: 'templateId'
        },
        {
          title: '操作',
          key: 'operator',
          align: 'center',
          type: 'button',
          options: {
            data: [
              {
                name: '置为电商模板',
                type: 'success',
                click: this.modifyTemplate
              }
            ]
          }
        }
      ]
    }
  },
  methods: {
    modifyTemplate (index, params) {
      if (params.templateId === 33554514) {
        this.$Modal.warning({
          title: '提示',
          content: '该频道已经是电商模板，无需修改！'
        })
        return
      }
      this.$Modal.confirm({
        title: '确认操作',
        content: '请确认您清楚此操作可能带来的后果，并确认您与主播已经协调沟通过。确认要将直播间模板设置为电商模板吗？',
        onOk: () => {
          this.$http.post('/tools/modifyTemplate', {sid: params.sid})
            .then(resp => {
              if (!RespUtils.checkResponse(resp, this)) {
                return
              }
              this.$Notice.success({
                title: '修改成功',
                duration: 5
              })
              this.$refs.liveTemplateMgr.search()
            })
        }
      })
    }
  }
}
</script>



