<template>
  <Row style="min-height: 400px;">
    <Card>
      <p>说明：</p>
      <p>1. 此工具导出的是已入驻的，保证金充足的主播列表，已剔除测试账号。</p>
      <p>2. 选择需要导出的品类，然后点导出即可。</p>
    </Card>
    <common-mgr
      ref="groupMgr"
      exportUrl="/tools/sellerExporter"
      :filters="filters"
      :showNew="false"
      mode="export"></common-mgr>
  </Row>
</template>
<script>
import CommonMgr from '@/components/CommonMgr'

export default {
  name: 'seller-exporter',
  components: {CommonMgr},
  data () {
    return {
      filters: [
        {
          name: '经营品类',
          key: 'category',
          type: 'select',
          width: '170px',
          options: {
            list: [
              {
                key: 'all',
                name: '全部'
              },
              {
                key: '翡翠玉石',
                name: '翡翠玉石'
              },
              {
                key: '翡翠玉石（广）',
                name: '翡翠玉石（广）'
              },
              {
                key: '彩色宝石',
                name: '彩色宝石'
              },
              {
                key: '文玩珠串',
                name: '文玩珠串'
              },
              {
                key: '手工艺',
                name: '手工艺'
              },
              {
                key: '其他',
                name: '其他'
              }
            ]
          }
        }
      ]
    }
  }
}
</script>



