const RespUtils = {
  checkResponse (resp, context) {
    console.log(resp)
    if (resp.status !== 200 || !resp.data) {
      context.$Notice.error({
        title: '请求失败，网络异常',
        duration: 5
      })
      return false
    }
    if (resp.data.code !== 200) {
      context.$Notice.error({
        title: '请求失败，原因:' + resp.data.msg,
        duration: 5
      })
      return false
    }
    return true
  }
}

export default RespUtils



